.container {
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  background-color: #fffbef;
}
@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
